html, body {
    margin: 0;
    overflow: hidden;
    background:
    radial-gradient(hsl(0, 0%, 27%) 4%, hsl(0, 0%, 18%) 9%, hsla(0, 0%, 20%, 0) 9%) 0 0,
    radial-gradient(hsl(0, 0%, 27%) 4%, hsl(0, 0%, 18%) 8%, hsla(0, 0%, 20%, 0) 10%) 50px 50px,
    radial-gradient(hsla(0, 0%, 30%, 0.8) 20%, hsla(0, 0%, 20%, 0)) 50px 0,
    radial-gradient(hsla(0, 0%, 30%, 0.8) 20%, hsla(0, 0%, 20%, 0)) 0 50px,
    radial-gradient(hsla(0, 0%, 20%, 1) 35%, hsla(0, 0%, 20%, 0) 60%) 50px 0,
    radial-gradient(hsla(0, 0%, 20%, 1) 35%, hsla(0, 0%, 20%, 0) 60%) 100px 50px,
    radial-gradient(hsla(0, 0%, 15%, 0.7), hsla(0, 0%, 20%, 0)) 0 0,
    radial-gradient(hsla(0, 0%, 15%, 0.7), hsla(0, 0%, 20%, 0)) 50px 50px,
    linear-gradient(45deg, hsla(0, 0%, 20%, 0) 49%, hsla(0, 0%, 0%, 1) 50%, hsla(0, 0%, 20%, 0) 70%) 0 0,
    linear-gradient(-45deg, hsla(0, 0%, 20%, 0) 49%, hsla(0, 0%, 0%, 1) 50%, hsla(0, 0%, 20%, 0) 70%) 0 0;
    background-color: #300;
    background-size: 100px 100px;
}

#root {
    height: 100vh;
    width: 100vw;
    text-align: center;
}

.full {
    width: 100%;
    height: 100%;
    padding: 1em;
    box-sizing: border-box;
}

canvas {
    cursor: pointer;
}
